


// VARIABLES

$homeColor: #f47c7c;
$codeColor: #70a1d7;
$musicColor: #a1de93;
$contactColor: #f7c28b; // #f7f48b


// IMPORTS

@import url('https://fonts.googleapis.com/css?family=Cormorant+Garamond:400,400i,700|Fira+Mono:400,700');


// Estimated width of sidebar, because px+% can't be added.
// Is it off? Yeah. Do I LOOK like some sort of scientist?
// No? Wow, that's awful rude of you. Judging someone based
// on their appearance. You should be ashamed. ASHAMED, I SAY.
$sidebarWidth: 520px;

// ACTUAL STYLING

p, a {
    font-family: "Nanum Myeongjo", serif; }

h1, h2, h3, h4, h5, h6 {
	font-family: "Fira Mono", monospace;
	font-weight: 400; }

// Minimap, per pagemap
#map {
	position: fixed;
	top: 10px;
	right: 10px;
	width: 500px;
	height: 20%;
	z-index: 100; }

.title {
	margin-left: 4%;
	margin-bottom: 2%;

	h1 {
		font-size: 5rem;
		margin-bottom: 0;
		margin-left: -19px; } } // Manually align to navigation

.content, .twoCol {
	display: flex; }


.sidebar {
	display: inline-block;
	margin-left: 4%;

	// Navigation
	ul {
		padding-left: 0;
		margin-top: 0; }

	ul li {
		font-size: 1.5rem;
		list-style: none;

		a {
			text-decoration: none;
			color: black;
			transition: all 0.1s;

			// As much as I'd love to sass-ify this, it'll take too much time.
			&.current {
				margin-left: -20px;
				padding-left: 10px; } } }


	.home:hover {
		color: $homeColor !important; }
	.code:hover {
		color: $codeColor !important; }
	.music:hover {
		color: $musicColor !important; }
	.contact:hover {
		color: $contactColor !important; } }


.articles {

	margin-top: 10%;
	margin-left: 5%;

	self-align: flex-start;

	.article {
		display: inline-block;
		margin: 0 auto;
		width: 60vw;
		padding-left: 40px;
		// border-top: 1px solid gray
		// border-bottom: 1px solid gray
		// text-align: center

		// Aligns top of paragraph with top of sidebar.
		// I'm insane, I know.
		p:first-child {
			margin-top: -2px; } } }
